.ele-wrap, .ele-wrap-selected {
    width: 32px;
    height: 32px;
    border-radius: 4px;
    position: relative;
    margin-bottom: 2px;
    margin-right: 2px;
    cursor: pointer;
}
.ele-wrap:hover {
    background-color: #6063F0 !important;
}
.ele-wrap:hover > .ele-number, .ele-wrap:hover > .ele-label {
    color: #FFFFFF !important;
}
.ele-wrap-selected {
    background-color: #6063F0 !important;
}
.ele-wrap-empty {
    width: 32px;
    height: 32px;
    margin-bottom: 2px;
    margin-right: 2px;
}
.ele-number {
    position: absolute;
    top: 0;
    right: 4px;
    height: 14px;
    line-height: 14px;
    font-size: 10px;
}
.ele-label {
    position: absolute;
    left: 4px;
    bottom: 0px;
    height: 24px;
    line-height: 24px;
    font-size: 16px;
    color: #555878;
}
